.App {
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: relative;
}

.App__video {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 100;
  width: 640px;
  height: 480px;
}

.App__video-container {
  position: relative;
  width: 640px;
  height: 480px;
  left: 50%;
  transform: translateX(-50%);
}

video::-webkit-media-controls {
  display: none;
}

/* Скрыть полосу прокрутки на мобильных устройствах (не обязательно) */
video::-webkit-media-controls-enclosure {
  display: none;
}